                transform: translate(0);
    }
}


.pop-up {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index:100;
    background: rgba(2, 20, 39, 0.479);
    -webkit-transition: all 1s;
    transition: all 1s;
    -webkit-animation-name: backAppear;
            animation-name: backAppear;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
}

.pop-up__window {
    display: flex;
    justify-content: space-around;
    background: white;
    border-radius: 10px;
    width: 50rem;
    height: 50rem;
    -webkit-animation-name: appear;
            animation-name: appear;
    -webkit-animation-duration: .7s;
            animation-duration: .7s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out; 
}

@-webkit-keyframes appear {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    80% {
        opacity: 0.8;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

}

@keyframes appear {
    0% {
        opacity: 0;
        -webkit-transform: scale(0.1);
                transform: scale(0.1);
    }
    80% {
        opacity: 0.8;
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }

}

@-webkit-keyframes backAppear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes backAppear {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
